// Homepage Carousel
import slick from "slick-carousel";

/* eslint-disable */
const articleCarousel = (() => {
	const initSlider = () => {
		const CLASSES = {};

		if ($(".js-article-carousel").length) {
			const slider = $(".js-article-carousel");

			
			//on first load, play the video
			$(".js-article-carousel.slider-for").on('init', function(event, slick, currentSlide) {
				var currentSlide, player, command;
				
				currentSlide = $(slick.$slider).find(".slick-current"); 
				if(currentSlide.find(".youtube-video").length){      
					currentSlide.find(".youtube-video")[0].contentWindow.postMessage('{"event":"command","func":"' + 'playVideo' + '","args":""}', '*');
				}
				// .contentWindow.postMessage('{"event":"command","func":"' + 'playVideo' + '","args":""}', '*');
			
			});
		
			//when new slide displays, play the video
			$(".js-article-carousel.slider-for").on("afterChange", function(event, slick) {
				var currentSlide;
				currentSlide = $(slick.$slider).find(".slick-current");
				if(currentSlide.find(".youtube-video").length){  
					currentSlide.find(".youtube-video")[0].contentWindow.postMessage('{"event":"command","func":"' + 'playVideo' + '","args":""}', '*');
				}
			});
			
			 
			//reset iframe of non current slide
			$(".js-article-carousel.slider-for").on('beforeChange', function(event, slick, currentSlide) {
				var currentSlide, iframe, clonedFrame;
				currentSlide = $(slick.$slider).find(".slick-current");
				if(currentSlide.find(".youtube-video").length){  
					currentSlide.find(".youtube-video")[0].contentWindow.postMessage('{"event":"command","func":"' + 'pauseVideo' + '","args":""}', '*');
				}     
			});
	
			//start the slider
			$('.js-article-carousel.slider-for').slick({
				slidesToShow: 1,
				arrows: true,
				centerMode: false,
				
				infinite: true,
				asNavFor: '.js-article-carousel.slider-nav',
				prevArrow:false,
				nextArrow:$(".slider-for .arrow-right"),
				mobileFirst:true,
				responsive:[
					{
						breakpoint:768,
						settings:{
							nextArrow:"<div class='arrow-right mb-32 lg:hidden'><svg width='15' height='22' viewBox='0 0 15 22' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1.64423 4.46632L1.29692 4.11462L1.64461 3.76329L3.73773 1.64829L4.09312 1.28919L4.4485 1.64829L13.3554 10.6483L13.7035 11L13.3554 11.3517L4.4485 20.3517L4.09312 20.7108L3.73773 20.3517L1.64461 18.2367L1.29692 17.8854L1.64423 17.5337L8.09622 11L1.64423 4.46632Z' stroke='black'/></svg></div>"
						}
					}
				]
			
			});
		}

		$('.js-article-carousel.slider-nav').slick({
			slidesToShow:4,
			slidesToScroll:1,
			arrows: true,
			infinite: true,
			focusOnSelect: true,
			asNavFor: '.js-article-carousel.slider-for',
		
			prevArrow:false,
			nextArrow:"<div class='arrow-right'><svg width='15' height='22' viewBox='0 0 15 22' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1.64423 4.46632L1.29692 4.11462L1.64461 3.76329L3.73773 1.64829L4.09312 1.28919L4.4485 1.64829L13.3554 10.6483L13.7035 11L13.3554 11.3517L4.4485 20.3517L4.09312 20.7108L3.73773 20.3517L1.64461 18.2367L1.29692 17.8854L1.64423 17.5337L8.09622 11L1.64423 4.46632Z' stroke='black'/></svg></div>",
			responsive:[
				{
					breakpoint:767,
					settings:{
					 slidesToShow:2
					}
				},
				{
					breakpoint:991,
					settings:{
				
						slidesToShow:3
					}
				},
				{
					breakpoint:1200,
					settings:{
				
						slidesToShow:3
					}
				}
			]
		
		});
	};

	const init = () => {
		initSlider();
	};

	return {
		init
	};
})();

export default articleCarousel;
