/* eslint-disable */
const header = (() => {
	const headerMenuToggle = () => {
		$(".navbar-trigger").on("click",function(){
           
                if(!$(".navbar-dropdown").is(":visible")){
                
                    $(".navbar-overlay").addClass("active");
                    $(".navbar-trigger").addClass("active");
                    $(".navbar-dropdown").fadeIn().addClass("active");
                    
                    
                    if($(window).width() < 992){
                        // var _navbarDropdownHeight = $(".navbar-dropdown").width();
                        // $(".blocks .block").css({
                        //     "paddingLeft":_navbarDropdownHeight+"px"
                        // });
                    }
                    if($(window).width() > 991){
                        $(".navbar-trigger .text").fadeOut();
                        $(".navbar").addClass("active");
                    }
                }else{
                    $(".navbar").removeClass("active");
                    $(".navbar-trigger").removeClass("active");
                    $(".navbar-dropdown").removeClass("active").fadeOut(100,function(){
                        $(".navbar-trigger .text").fadeIn();
                        $(".navbar-overlay").removeClass("active");
                  
                    });
                    if($(window).width() < 992){
                        $(".blocks .block").css({
                            "paddingLeft":0
                        });
                    }
                }
            

        });

        $(".navbar-overlay").on("click",function(){
            $(".navbar-trigger").click();
        });
    };
    
    const calculateHeaderMenuHeight = () => {
        var wWidth = $(window).width();
        if(wWidth < 992 && $(".heading-2").length){
           var _navbarDropdownHeight= $(".heading-2 .navbar-dropdown").outerHeight() / 1.5;
           var _target =$(".js-padding-top");
           _target.css("paddingTop", _navbarDropdownHeight+"px");
        }
    }
	

	const init = () => {
        headerMenuToggle();
        calculateHeaderMenuHeight();
	};

	return {
		init
	};
})();

export default header;
