/* eslint-disable */
const navbarDropdown = (() => {
	const toggleInit = () => {
		const CLASSES = {};
		$(".trigger-toggle").on("click", function(e) {
			e.preventDefault();

			var _this = $(this);
			var _target = _this
				.closest(".navbar-item.has-dropdown")
				.find(".navbar-dropdown");

			if (_target.is(":visible")) {
				_this.removeClass("active");
				_target.slideUp();
			} else {
				_this.addClass("active");
				_target.slideDown();
			}
		});
		$(document).on("click", ".navbar-dropdown__trigger", function(e) {
			e.preventDefault();
			var _this = $(this);
			var _target = _this
				.closest(".menu-item-has-children")
				.find("> .sub-menu");
			if (_target.is(":visible")) {
				_this.removeClass("active");
				_target.slideUp();
			} else {
				_this.addClass("active");
				_target.slideDown();
			}
		});
	};

	const init = () => {
		toggleInit();
	};

	return {
		init
	};
})();

export default navbarDropdown;
