/* eslint-disable */
const filter = (() => {
	const toggleContent = () => {
		const CLASSES = {};

		if ($(".js-filter").length) {
			$(".js-filter label").on("click", function() {
                var _this = $(this);
                var target = _this.parent().find("input").prop("value");

               console.log(target);
               if(target != 'all'){
                    $(".filter-body .filter-row").css({"display":"none"});
                    $(".filter-body .filter-row[data-type='"+target+"']").css({"display":"flex"});
               }else{
                    $(".filter-body .filter-row").css({"display":"flex"});
               }

			});
		}
	};

	const init = () => {
		toggleContent();
	};

	return {
		init
	};
})();

export default filter;
