;(function(){
    function id(v){return document.getElementById(v); }
    function loadbar() {
      var ovrl = id("loader"),
        stat = id("stat"),
          img = document.images,
          c = 0;
          tot = img.length;
  
      function imgLoaded(){
        c += 1;
        var perc = ((100/tot*c) << 0) ;
    
            ovrl.style.opacity = 1 - (perc/100);
        stat.innerHTML =  perc;
        if(c===tot) return doneLoading();
       
        
      }
      function doneLoading(){
        setTimeout(function(){ 
            ovrl.style.opacity = 0;
        }, 1200);
        
        setTimeout(function(){ 
          ovrl.style.display = "none";
        }, 2000);
      }
      for(var i=0; i<tot; i++) {
        var tImg     = new Image();
        tImg.onload  = imgLoaded;
        tImg.onerror = imgLoaded;
        tImg.src     = img[i].src;
      }    
    }
    document.addEventListener('DOMContentLoaded', loadbar, false);
  }());