// Homepage Carousel
import slick from "slick-carousel";

/* eslint-disable */
const testimonialCarousel = (() => {
	const initSlider = () => {
		const CLASSES = {};

		if ($(".js-testimonial-carousel").length) {
			const slider = $(".js-testimonial-carousel");

			slider.slick({
				rows: 0,
				dots: true,
				arrows: false,
				autoplay: true,
				autoplaySpeed: 4000,
				// prevArrow: $(".js-banner-carousel-prev"),
				// nextArrow: $(".js-banner-carousel-next"),
				// appendDots: $(".js-banner-carousel-pagination"),
				cssEase: "cubic-bezier(0.645, 0.045, 0.355, 1)",
				speed: 500
			});
		}
	};

	const init = () => {
		initSlider();
	};

	return {
		init
	};
})();

export default testimonialCarousel;
