/* eslint-disable */
const goToTop = (() => {
	const scrollListener = () => {
		$(window).scroll(function() {
			var _wd = $(this);
			var topBtn = $(".c-top-btn");
			if (_wd.scrollTop() > _wd.height() / 2) {
				topBtn.addClass("active");
			} else {
				topBtn.removeClass("active");
			}
		});
	};
	const clickListener = () => {
		const CLASSES = {};
		$(document).on("click", ".js-to-top-btn", function() {
			$("html,body").animate({ scrollTop: 0 }, 500);
		});
	};

	const init = () => {
		clickListener();
		scrollListener();
	};

	return {
		init
	};
})();

export default goToTop;
