/* eslint-disable */
const navbarToggler = (() => {
	const toggleInit = () => {
		const CLASSES = {};
		$(document).on("click", ".js-navbar__toggler", e => {
			var _this = $(".js-navbar__toggler");
			var _target = $(".c-navbar__collapse");

			if ($(".c-navbar__collapse").is(":visible")) {
				_this.removeClass("is-active");
				_target.slideUp(300);
			} else {
				_this.addClass("is-active");
				_target.slideDown(300);
			}
		});

		var arrow =
			'<span class="navbar-dropdown__trigger trigger-toggle inline-block ml-8"><svg xmlns="http://www.w3.org/2000/svg" width="8.681" height="5.048" viewBox="0 0 8.681 5.048"><path id="arrow-down-thin" d="M4063.291,4726.4l3.987,3.987,3.987-3.987" transform="translate(-4062.938 -4726.044)" fill="none" stroke="#333" stroke-width="1"/></svg></span>';

		$(document)
			.find(".menu-item-has-children")
			.append(arrow);
	};

	const init = () => {
		toggleInit();
	};

	return {
		init
	};
})();

export default navbarToggler;
