const YTPlayer = require("yt-player");
/* eslint-disable */
const homePageFullScreenVideo = (() => {
	const calculateFullHeight = () => {
		const CLASSES = {};
		function fullScreen() {
			if ($(window).width() > 991) {
				if ($(".js-video-player").length) {
					var _header = $(".c-header");
					$(".js-video-player").css(
						"height",
						$(window).height() - _header.height()
					);
				}
			}

			if ($(window).width() > 991) {
				if ($(".js-full-image").length) {
					var _header__top = $(".c-header__top");
					var _header__bottom = $(".c-header__bottom");
					$(".js-full-image").css(
						"height",
						$(window).height() -
							(_header__top.height() + _header__bottom.height())
					);
				}
			}
		}
		fullScreen();
		$(window).resize(function() {
			fullScreen();
		});
	};
	function get_youtubeId(url) {
		var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
		var match = url.match(regExp);
		if (match && match[2].length == 11) {
			return match[2];
		} else {
			//error
		}
	}

	const urlVideoIntegrate = () => {
		var _targetParent = $(".js-video-player");
		var url = _targetParent.data("embed");
		var autoplay = _targetParent.data("autoplay") === true ? 1 : 0;
		var loop = _targetParent.data("loop") === true ? 1 : 0;
		var muted = _targetParent.data("muted") === true ? 1 : 0;
		var youTubeVideoId = get_youtubeId(url);
		var videoFrame = "<div id='videoFrame'></div>";
		var container = _targetParent.parent();
		/*
		var video = `<iframe width='100%' class='youtube-player' src='https://www.youtube.com/embed/${youTubeVideoId}?autoplay=1&modestbranding=1&controls=0&showinfo=0&rel=0&enablejsapi=1&version=3&loop=0&playerapiid=ytplayer&allowfullscreen=true&wmode=transparent&iv_load_policy=3&html5=1&playlist=iApVVKsF94E&disablekb=true' frameborder='0' mozallowfullscreen webkitallowfullscreen allowfullscreen></iframe>`;
		$("#videoFrame iframe").css({
			width: container.width() + "px",
			height: container.width() * (9 / 16) + "px",
			position: "absolute",
			marginTop: -container.width() * (9 / 32) + "px",
			top: "50%"
        });
        */
		$(window).on("resize load", function() {
			$("iframe").each(function() {
				var self = $(this);
				var container = self.parent();

				self.css({
					width: container.width() + "px",
					height: container.width() * (9 / 16) + "px",
					position: "absolute",
					marginTop: -container.width() * (9 / 32) + "px",
					top: "50%"
				});
			});
		});

		// Load the IFrame Player API code asynchronously.
		var tag = document.createElement("script");
		tag.src = "https://www.youtube.com/player_api";
		var firstScriptTag = document.getElementsByTagName("script")[0];
		firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

		var player;
		function onYouTubePlayerAPIReady() {
			player = new YT.Player("yt-player", {
				autoplay: 1,
				videoId: "yZM5VAKi5vU",
				playerVars: {
					autoplay: 1,
					rel: 0,
					showinfo: 0,
					showsearch: 0,
					controls: 0,
					loop: 1,
					enablejsapi: 1,
					playlist: "yZM5VAKi5vU"
				},
				events: {
					onReady: onPlayerReady
					//"onStateChange": onPlayerStateChange
				}
			});
		}

		function onPlayerReady(event) {
			event.target.mute();
			event.target.playVideo();
		}

		function onPlayerStateChange(event) {
			var id = "yZM5VAKi5vU";

			if (event.data === YT.PlayerState.ENDED) {
				player.loadVideoById(id);
			}
		}
	};

	const init = () => {
		calculateFullHeight();
		//urlVideoIntegrate();
	};

	return {
		init
	};
})();

export default homePageFullScreenVideo;
