/* eslint-disable */
const accordion = (() => {
	const toggleContent = () => {
		const CLASSES = {};

		if ($(".c-accordion__wrapper").length) {
			$(".c-accordion__item .c-accordion__header").on("click", function() {
				var _this = $(this);
				// _this
				//   .closest('.c-accordion__wrapper')
				//   .find('.c-accordion__item')
				//   .removeClass('accordion-active')
				//   .find('.c-accordion__item--body')
				//   .slideUp();
				if (_this.next(".c-accordion__body").is(":visible")) {
					_this.next(".c-accordion__body").slideUp();
					_this.parent(".c-accordion__item").removeClass("accordion-active");
				} else {
					_this.next(".c-accordion__body").slideDown();
					_this.parent(".c-accordion__item").addClass("accordion-active");
				}
			});
		}
	};

	const init = () => {
		toggleContent();
	};

	return {
		init
	};
})();

export default accordion;
