// Homepage Carousel
import slick from "slick-carousel";

/* eslint-disable */
const subCategoryCarousel = (() => {
	const initSlider = () => {
		const CLASSES = {};

		if ($(".js-sub-category-carousel").length) {
			const slider = $(".js-sub-category-carousel");

			slider.slick({
				draggable:true,
				dots: false,
				arrows: true,
				prevArrow:false,
				nextArrow:"<div class='arrow-right'><svg width='15' height='22' viewBox='0 0 15 22' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1.64423 4.46632L1.29692 4.11462L1.64461 3.76329L3.73773 1.64829L4.09312 1.28919L4.4485 1.64829L13.3554 10.6483L13.7035 11L13.3554 11.3517L4.4485 20.3517L4.09312 20.7108L3.73773 20.3517L1.64461 18.2367L1.29692 17.8854L1.64423 17.5337L8.09622 11L1.64423 4.46632Z' stroke='black'/></svg></div>",
				autoplay: true,
				autoplaySpeed: 5000,
				pauseOnHover: false,
				vertical:true,
				slidesToShow:2,
				slidesToScroll:1,
				cssEase: "cubic-bezier(0.645, 0.045, 0.355, 1)",
				speed: 500,
				mobileFirst:true,
				responsive:[
					{
						breakpoint:768,
						settings:{
							vertical:false
						}
					}
				]
			});
		}
	};

	const init = () => {
		initSlider();
	};

	return {
		init
	};
})();

export default subCategoryCarousel;
