var $ = require("jquery");
import "lightbox2/dist/css/lightbox.css";
var lightbox = require("lightbox2");

/* eslint-disable */
const zoomImage = (() => {
	const zoomInit = () => {
		lightbox.option({ disableScrolling: true });
	};

	const init = () => {
		$(document).ready(function() {
			zoomInit();
		});
	};

	return {
		init
	};
})();

export default zoomImage;
