var $ = require("jquery");
import "jquery-match-height";

/* eslint-disable */
const equalHeight = (() => {
	const init = () => {
		$(document).ready(function() {
			var options = {
				byRow: true,
				property: "min-height"
			};
			$(".js-equal-height").matchHeight(options);
		});
	};

	return {
		init
	};
})();

export default equalHeight;
