/* eslint-disable */
const gearRentals = (() => {
    var selectedList = [];
    var total = 0;
	const numberIncrement = () => {

        $(".c-number-counter .btn-add").on("click",function(){
            var parent =  $(this).closest(".c-table-row");
            var price = parent.find(".price").data("num");
            var target = parent.find(".total").find(".num");
            var input = $(this).parent().find("input");
            var inputNum = Number(input.val()) + 1;
            var id = parent.data("id");
            input.val(inputNum);
            target.text(Number(price) * inputNum);

            var objIndex = selectedList.findIndex((obj => obj.id == id));
            selectedList[objIndex].days = inputNum;
            selectedList[objIndex].price = Number(price) * inputNum;
            
            total = selectedList.reduce((prev,next)=> {return prev + next.price},0);
            $(".all-total").text("$"+total);
        });

        $(".c-number-counter .btn-dec").on("click",function(){
           
            var parent =  $(this).closest(".c-table-row");
            var price = parent.find(".price").data("num");
            var target = parent.find(".total").find(".num");
            var input = $(this).parent().find("input");
            var inputNum =Number(input.val()) > 1? Number(input.val()) - 1:1;
            input.val(inputNum);
            target.text(inputNum > 0?Number(price) * inputNum:price);
            var id = parent.data("id");
            var objIndex = selectedList.findIndex((obj => obj.id == id));
            selectedList[objIndex].days = inputNum;
            selectedList[objIndex].price = Number(price) * inputNum;
           

            total = selectedList.reduce((prev,next)=> {return prev + next.price},0);
            $(".all-total").text("$"+total);
        });
    
    }

    const handleCheckbox = () => {
    
        $(".c-table .table-row-overlay").on("click",function(e){
            $(this).addClass("hidden");
            
            $(this).parent().addClass('active').find(".c-checkbox input").prop("checked", true);
            var _parent = $(this).closest(".c-table-row");
            var _id = _parent.data("id");
            var _title= _parent.data("title");
            var _days=_parent.data("days");
            var _price=_parent.data("price");
            var toAdd = {
                id:_id,
                title:_title,
                days:_days,
                price:_price
            }
            selectedList.push(toAdd);
            
            var total = selectedList.reduce((prev,next)=> {return prev + next.price},0);
            $(".all-total").text("$"+total);
                        
        }); 

        $(".c-table .c-checkbox label").on("click",function(){
           
            if($(this).parent().find("input").is(":checked")){
              var parent =  $(this).closest(".c-table-row");
              $(this).parent().find("input").val("1").trigger('change');
              console.log($(this).parent().find("input"));
               $(this).parent().find("input").removeProp("checked");
               $(this).closest(".c-table-row").find(".table-row-overlay").removeClass("hidden");
               var parent =  $(this).closest(".c-table-row");
               var id = parent.data("id");
               var objIndex = selectedList.findIndex((obj => obj.id == id));
               selectedList.splice(objIndex, 1);

            var total = selectedList.reduce((prev,next)=> {return prev + next.price},0);
            $(".all-total").text("$"+total);
            }         
        });

    }
    const handleFormSubmit = () => {
        if($("#gear-rentals-booking-btn").length){
            var button =$("#gear-rentals-booking-btn");
            var target = button.data('redirect');
            button.on("click",function(){
                if(selectedList.length){
                    sessionStorage.setItem("selectedGeneralRentals",JSON.stringify(selectedList));
                     window.location.href=target;
                }
            });
        }
    }
	const init = () => {
        numberIncrement();
        handleCheckbox();
        handleFormSubmit();
	};

	return {
		init
	};
})();

export default gearRentals;
